.xt-main-container{
    display: grid;
    height: 100vh;
    width: 100vw;
    grid-template-areas: 
        "header"
        "headerMenu"
        "main"
        "footer";
    grid-template-columns: auto;
    grid-template-rows: 60px auto 1fr 50px;
}

.xt-grid-header {
    grid-area: header;
    background-color: rgb(223, 223, 89);
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: badComa;
    font-size: 80px;
    height: 50px;
}   

.xt-grid-header-menu {
    grid-area: headerMenu;
}

.xt-grid-main {
    grid-area: main;
    
}

.xt-grid-footer {
    grid-area: footer;
    color: antiquewhite;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.xt-top {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
}

.xt-table-view {
    margin-top: 40px;
    overflow-x: hidden;
    overflow-y: scroll;
}

.xt-modal {
    position: absolute;
    left: 100px;
    top: 100px;
    height: fit-content;

}

.xt-add-item-modal-flex {
    display: flex;
    flex-direction: row;
}

.xt-modal-add-task-btn {
    position: absolute;
    right: 250px;
    top: 55px;
    height: 40px; 
    width: 150px;
}

.xt-modal-quick-add-task-btn {
    position: absolute;
    right: 450px;
    top: 55px;
    height: 40px; 
    width: 150px;
}

.xt-comment-alert {
    height: 20px;
    width: 260px;
    font-size: smaller;
    padding: 1px;
}
